import React from 'react';

import Pagination from '../molecules/Pagination';
import PostListItem from '../molecules/PostListItem';

const PostList = ({ data, page, path, pageListSize }) => {
  console.log('PostList--path', path);
  return (
    <React.Fragment>
      {data.map(
        ({ node }) =>
          console.log('node', node) || (
            <PostListItem key={node.fields.slug} node={node} />
          )
      )}
      {page && <Pagination listSize={pageListSize} page={page} path={path} />}
    </React.Fragment>
  );
};

export default PostList;
